import Skeleton from "@material-ui/lab/Skeleton";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";

export default function SkeletonUserRasp() {
  return (
    <div className="rasp-slider">
      <div className="section-title">Timetable</div>

      <div className="profiles" style={{ marginBottom: "15px" }}>
        <div className="items">
          {[6, 14, 29].map((item, i) => (
            <Skeleton variant="rect" key={i} style={{ marginRight: "8px" }}>
              <div className="item">{item.name}</div>
            </Skeleton>
          ))}
        </div>
        <Skeleton variant="rect">
          <div className="edit item">
            <CompareArrowsIcon />
          </div>
        </Skeleton>
      </div>

      <div className="slick-slider" style={{ padding: "0 20px" }}>
        <div className="slide">
          <div className="content">
            <div className="title">
              <Skeleton variant="rect" style={{ margin: "0 auto" }}>
                <div className="date">21  2021</div>
              </Skeleton>
              <Skeleton
                variant="rect"
                style={{ margin: "0 auto", marginTop: "4px" }}
              >
                <div className="week"></div>
              </Skeleton>
            </div>

            {[1, 2, 3, 4].map((time, i) => (
              <div className="slide-row" key={i}>
                <div
                  className="main-info"
                  style={{ display: "flex", marginRight: "10px" }}
                >
                  <div style={{ marginRight: "6px" }}></div>
                  <Skeleton variant="rect">
                    <div className="time">14:55</div>
                  </Skeleton>
                  <div className="f-right">
                    <div className="badges">
                      <Skeleton variant="rect" style={{ marginRight: "6px" }}>
                        <div className="badge"></div>
                      </Skeleton>
                    </div>
                    <Skeleton variant="rect">
                      <div className="audience">4/103</div>
                    </Skeleton>
                  </div>
                </div>
                <div className="second-info" style={{ flexGrow: 1 }}>
                  <div style={{ display: "flex" }}>
                    <div style={{ marginRight: "6px" }}></div>
                    <Skeleton variant="rect">
                      <div className="discipline">/</div>
                    </Skeleton>
                  </div>
                  <div style={{ display: "flex" }}>
                    <div style={{ marginRight: "6px" }}></div>
                    <Skeleton variant="rect">
                      <div className="teacher"> ..</div>
                    </Skeleton>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
