import React from "react";
import SlideRowComponent from "./SlideRowComponent";
import { store } from "../../store";
import moment from "moment";
import sticker from "../../images/honka_animated-256px-14.gif";

const months = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

const weekDayNames = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

export default function SlideComponent({ data, index }) {
  const state = store.getState().userState;
  const reqWeek = state.raspData.reqWeek;

  if (!data) {
    return (
      <SkeletonUserRasp
        day={moment(reqWeek, "YYYY-MM-DD")
          .add(state.dayofweek.replace(0, 7) - 1, "days")
          .format("DD")}
        month={moment(reqWeek, "YYYY-MM-DD").format("MM")}
        year={moment(reqWeek, "YYYY-MM-DD").format("YYYY")}
        dayofweek={state.dayofweek.replace(0, 7)}
      />
    );
  }

  const slides = Object.keys(data).sort(
    (a, b) => +a.split(".")[0] - +b.split(".")[0]
  );

  const day = moment(reqWeek, "YYYY-MM-DD").add(index, "days").format("DD");
  const month = moment(reqWeek, "YYYY-MM-DD").add(index, "days").format("MM");
  const year = moment(reqWeek, "YYYY-MM-DD").format("YYYY");

  return (
    <div className="slide">
      <div className="content">
        <div className="title">
          <div className="date">
            {day} {months[month - 1]} {year}
          </div>
          <div className="week">{weekDayNames[index]}</div>
        </div>

        {slides.map((time, i) => (
          <SlideRowComponent lesson={data[time]} time={time} key={i} />
        ))}
      </div>
    </div>
  );
}

function SkeletonUserRasp({ day, month, year, dayofweek }) {
  return (
    <div className="slick-slider" style={{ padding: "0 20px" }}>
      <div className="slide">
        <div className="content">
          <div className="title">
            <div className="date">
              {day} {months[month - 1]} {year}
            </div>
            <div className="week">{weekDayNames[dayofweek - 1]}</div>
          </div>

          {[1, 2, 3, 4].map((time, i) => (
            <div
              className="slide-row"
              key={i}
              style={{
                border: "none",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              {i === 0 ? "    " : ""}
            </div>
          ))}

          <img
            src={sticker}
            alt="..."
            style={{
              position: "absolute",
              bottom: "-6px",
              left: "50%",
              width: "150px",
              transform: "translateX(-50%)",
            }}
          />
        </div>
      </div>
    </div>
  );
}
