import { connect } from "react-redux";
import { changeActiveProfile, deleteUserProfile } from "../actions";
import Slider from "../components/RaspSlider";

const mapStateToProps = (state) => {
  return {
    user: state.userState.user,
    profiles: state.userState.profiles,
    raspData: state.userState.raspData,
    nextweekData: state.userState.nextweekData,
  };
};

const mapDispatchToProps = { changeActiveProfile, deleteUserProfile };

export default connect(mapStateToProps, mapDispatchToProps)(Slider);
