import React from "react";

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SlideComponent from "./SlideComponent";
import { store } from "../../store";

function SliderComponent({ raspData }) {
  const dayofweek =
    +store.getState().userState.dayofweek > 5 ||
    +store.getState().userState.dayofweek === 0
      ? 5
      : +store.getState().userState.dayofweek;

  const settings = {
    dots: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    centerMode: true,
    centerPadding: "20px",
    infinite: true,
    arrows: false,
    initialSlide: dayofweek - 1,
  };

  if (raspData.data.length === 0) {
    return <SlideComponent />;
  }

  return (
    <Slider key={dayofweek} {...settings}>
      {raspData.data.map((data, index) => (
        <SlideComponent key={index} data={data} index={index} />
      ))}
    </Slider>
  );
}

export default SliderComponent;
