import React, { useState } from "react";
import ArrowLeftIcon from "@material-ui/icons/ArrowLeft";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import { store } from "../../store";

function SlideRowComponent({ lesson, time }) {
  const userType = store.getState().userState.profiles.activeRasp.type;
  const [minIndex, setMinIndex] = useState(0);
  
  //   
  if (lesson.length === 0) {
    return (
      <div className="slide-row" style={{ justifyContent: "center" }}>
        <div className="main-info">
          <div className="time"> {time}</div>
        </div>
      </div>
    );
  }

  if (userType === "teach") {
    const sdiscipline = lesson.discipline.split("-");
    const type = sdiscipline.pop();
    const discipline = sdiscipline.join("-");
    return (
      <div className="slide-row">
        <div className="main-info">
          <div className="time"> {time}</div>
          <div className="f-right">
            <div className="badges">
              <div className="badge">{type}</div>
            </div>
            <div className="audience">
              <span>{lesson.lectureHall.replace("", "")}</span>
            </div>
          </div>
        </div>
        <div className="second-info" style={{ flexGrow: 1 }}>
          <div className="discipline"> {discipline}</div>
          <div className="teacher">
            <div className="shadow"></div>
             {lesson.groups}
          </div>
        </div>
      </div>
    );
  }

  // if userType === stud
  const sdiscipline = lesson[minIndex].discipline.split("-");

  const type = sdiscipline.pop();
  const discipline = sdiscipline.join("-").trim();
  let lectureHall = lesson[minIndex].lectureHall.replace("", "");
  const teacher = lesson[minIndex].teacher;

  const subStatus = lectureHall.indexOf(":") !== -1;
  const subgroup = subStatus && ":" + lectureHall.split(":")[1];

  if (discipline === "") {
    lectureHall = "";
  }

  const Badge = () => (
    <div
      className="badge"
      onClick={() => {
        const next = minIndex + 1;
        if (next > lesson.length - 1) {
          setMinIndex(0);
        } else {
          setMinIndex(next);
        }
      }}
    >
      {lesson.length === 1 ? (
        subgroup
      ) : (
        <React.Fragment>
          <ArrowLeftIcon className="arrow-left" />
          {subgroup}
          <ArrowRightIcon className="arrow-right" />
        </React.Fragment>
      )}
    </div>
  );

  return (
    <div className="slide-row">
      <div className="main-info">
        <div className="time"> {time}</div>
        <div className="f-right">
          <div className="badges">
            <div className="badge">{type}</div>
            {subgroup && <Badge />}
          </div>
          <div className="audience">
            <span>
              {subgroup ? lectureHall.replace(subgroup, "") : lectureHall}
            </span>
          </div>
        </div>
      </div>
      <div className="second-info" style={{ flexGrow: 1 }}>
        <div className="discipline"> {discipline}</div>
        <div className="teacher">
          <div className="shadow"></div>
           {teacher}
        </div>
      </div>
    </div>
  );
}

export default SlideRowComponent;
