import ProfilesComponent from "../Profiles";
import SliderComponent from "./Slider";
import SelectUserForm from "./SelectUserForm";
import SkeletonUserRasp from "./SkeletonUserRasp";

import { setNextweekData } from "../../actions";
import { store } from "../../store";

export default function RaspSliderComponent({
  profiles,
  raspData,
  nextweekData,
  changeActiveProfile,
  deleteUserProfile,
}) {
  if (!profiles.rasp) {
    return (
      <div className="rasp-slider">
        <div className="section-title">Timetable</div>
        <SelectUserForm />
      </div>
    );
  }

  if (!raspData) return <SkeletonUserRasp />;

  return (
    <div className="rasp-slider">
      <div className="section-title">Timetable</div>
      <ProfilesComponent
        type={"rasp"}
        profiles={profiles.rasp}
        active={profiles.activeRasp}
        changeActiveProfile={changeActiveProfile}
        deleteUserProfile={deleteUserProfile}
      />
      <SliderComponent raspData={raspData} />
      {nextweekData.data.length !== 0 ? (
        <div
          className="nextweek"
          onClick={(e) => {
            e.preventDefault();
            store.dispatch(setNextweekData());
          }}
        >
             . 
        </div>
      ) : (
        ""
      )}
    </div>
  );
}
